.Note
{
    min-height:5.5rem;
    
    display:flex;
    flex-direction:column;
    align-items:center;

    font-size:1rem;
    .container
    {
        width:30%;
        height:5rem;
        border-width:1px;
        border-radius:10px;
        text-align:center;

        display:flex;
        flex-direction:column;
        z-index:0;
        background-color:white;
        opacity:0.8;
        text-align: center;
        animation: pojawianie;
        animation-duration:0.5s;
        animation-timing-function: ease-in-out;

       

        textarea
        {
            width:100%;
            height:100%;
            outline:none;
            border:none;
            font-size:20px;
            text-align:center;
            pointer-events:none;
            color:black;

        }
    }

}

.Buttongroup
{

    display:flex;
    flex-direction:column;
    z-index:1;
    text-align:center;
    margin-left:30%;
    margin-top:-5rem;
    animation: pojawianie;
    animation-duration:0.5s;
    animation-timing-function: ease-out;
}
.done
{
    background-color:black;
    color:white;
    width:2.5rem;
    height:2.5rem;
    border-radius:10px;
    outline:none;
    opacity:0.8;
    animation: pojawianie;
    animation-duration:0.5s;
    animation-timing-function: ease-out;
}
.done:hover
{

    opacity:1;
}
.del
{
    background-color:black;
    color:white;
    width:2.5rem;
    height:2.5rem;
    border-radius:10px;
    opacity:0.8;
    animation: pojawianie;
    animation-duration:0.5s;
    animation-timing-function: ease-out;
}
.del:hover
{

    opacity:1;
}


@keyframes pojawianie
{
    from
    {
        transform: translate(-100%);
       opacity:0;
    }

    to
    {
        transform: translate(0);
       opacity:0.8s;
    }
}