.NotesCreator
{
    min-height:20vh;
    display:flex;
    flex-direction:column;
    align-items:center;

    border:none;
    outline:none;

    textarea
    {
        resize:none;
        width:30%;
        border-radius:10px;
        text-align:center;
        justify-content: center;
        text-justify: center;
        outline:none;
        opacity:0.8;
        font-size:20px;
    }
    .done
    {
        width:20rem;
        background:none;
        border-radius:10px;
        outline:none;
       background-color:white;
       color:black;
    }


}
.Logo
{
    height:10rem;
}
